  .dropdown-menu.pull-none-xs {
    left: 0;
  }
  .hidden-xs.show {
    display: inherit !important;
  }
  .wrapper-lg {
    padding: 15px;
  }
  .padder-lg {
    padding-left: 15px;
    padding-right: 15px;
  }
}
